.secondary-nav {
  padding: 0 5px;
  margin: 0;
  list-style: none;
  order: -1;
  
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  flex: auto;
}

.secondary-nav__item {
  padding: 10px;
  font-size: 18px;
  line-height: 13px;
}
  
.secondary-nav__link {
  display: block;
  width: 100%;
  height: 100%;
  
  color: #fff;
  text-decoration: none;
  
  &.active {
    text-transform: uppercase;
    color: #d8b55b;
  }
  
  &:hover,
  &:focus {
    color: #d8b55b;
    text-decoration: none;
  }
}